.Controls > button:not(:last-child) {
  margin-right: 15px;
}

.Button {
  min-width: 80px;
  padding: 5px;
  color: white;
  background: orange;
  border: none;
  outline: none;
  border-radius: 5px;
  margin-bottom: 10px;
  text-transform: capitalize;

  transform: scale(1);
  transition: transform 250ms;
}

.Button:hover {
  transform: scale(1.1);
}
